/*siwei*/
@function tr($px){//$px为需要转换的字号 px-to-rem
    @return $px / 40px * 1rem; //40px为根字体大小
}
/*
.test {font-size: tr(150px);}	.test {font-size: 3.75rem; }
.test2{width:tr(220px);}			.test2 {width: 5.5rem; }
*/
@function wtb($px){
  @return $px/640px*100%;
}
@function htb($px){
  @return $px/1136px*100%;
}


